import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";

import Map from '../Mapa';

//otros
import Otros from "./Otros";
//formulario
import Formulario from './Formulario';

//core
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import container from "assets/img/contactos/soja.jpg";

const iframe = '<iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3600.0427804115498!2d-57.54488082014904!3d-25.5369518592352!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMjXCsDMyJzA4LjYiUyA1N8KwMzInNDMuMiJX!5e0!3m2!1ses!2spy!4v1574887469759!5m2!1ses!2spy" width="100%" height="300" frameborder="0" style="border:0;" allowfullscreen=""></iframe>'; 
function Iframe(props) {
  return (<div dangerouslySetInnerHTML={ {__html:  props.iframe?props.iframe:""}} />);
}

export default function Datos(props){

	const {classes,contenido} = props;
	const contenidoOtros = contenido.otros;
	const contenidoFormulario = contenido.formulario;
  const places = [
    {
      name: "Sydney",
      title: "Sydney",
      lat: -25.535732,
      lng: -57.545320,
      id: 1
    },
    ]

	return(
    <div className={classes.section} 
      style={{  
                      backgroundImage: "url(" + container + ")"
      }} id={"seccion-contactos"}>
      <GridContainer justify="center">
        <GridItem cs={6} sm={6} md={6} className={classes.resaltar}>
        <div>
            <h2 className={classes.title}>{contenido.titulo}</h2>
            <h4 className={classNames(classes.description,classes.colorBlanco)}>
              {contenido.descripcion}
            </h4>
            <Otros contenido={contenidoOtros} classes={classes}/>
        </div>
        </GridItem>

        <GridItem cs={6} sm={6} md={6} className={classNames(classes.resaltar,classes.formularioWrapper)}>
        	<Formulario classes={classes} contenido={contenidoFormulario}/>
        </GridItem>

        <GridItem cs={6} sm={6} md={12} className={classNames(classes.resaltar,classes.formularioWrapper)}>
          <Iframe iframe={iframe} />
        </GridItem>                
      </GridContainer>
    </div>			
	)
}