import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";

export default function Otros(props){
	const {classes,contenido} = props;

	const renderItem = () => {
        	let item = contenido.map( (registro,index) => {
	        	let valores = {}
				Object.keys(registro).forEach( (campo) =>{
					valores = registro[campo];
				})
				return(
		              <div className={classes.itemContact} key={"item_"+index}>
		                   <div className={classNames(classes.wrapperIcon)}>
		                     <svg className={classNames(classes.MuiSvgIcon, classes.svgIcon, classes.colorBlanco)} focusable="false" viewBox="0 0 24 24" aria-hidden="true" role="presentation"><path d={valores.path}></path></svg>
		                   </div>
		                   <div className = {classNames(classes.wrapperDescripcion,classes.colorBlanco)}>
		                         <h4 className={classes.subtituloContactos}>{valores.titulo}</h4>
		                         <div className="jss363">
		                           <span>
		                           	{valores.descripcion.split('\n').map((item, key) => {
									  return (
											<React.Fragment key={key}>{item}<br /></React.Fragment>
									  )
									})}
		                           </span>
		                         </div>
		                   </div>
		              </div>
				)
        	})
        	return item;
	}

	return(
        <div className={classes.sectionContact}>
        	{
        		renderItem()
        	}
             </div>

	)
}