import imgedb from 'assets/images/logo2.png';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    card: {
      maxWidth: 345,
      marginBottom: '10.25%', // 16:9
    },
    media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: "#3e82d9",
    },
	action : {
		color : "#e5e5e5"
	},
	edb : {
			backgroundImage: `url(${imgedb})`,
			width: "37px",
			height: "23px",
			backgroundSize: "52px 23px",
			backgroundColor: "#5f94de",
			backgroundRepeat: "no-repeat",
			borderRadius: "10%"
	}
  }),
);

const GlobalCss = withStyles({
  // @global is handled by jss-plugin-global.
  '@global': {
    // You should target [class*="MuiButton-root"] instead if you nest themes.
    '.MuiCardHeader-action': {
	    flex: "0 0 auto",
	    alignSelf: "flex-start",
	    marginTop: "9px",
	    marginRight: "-8px",
    },
  },
})(() => null);


export default function CustomCard(props) {
	const {isShowing, toggle} = useModal();
	
	const {introduccion,indice,titulo,img,descripcion,subheader="", layerSlider=""} = props.contenido
	const classes = useStyles();  
	const [expanded, setExpanded] = useState(false);
	const  [open, setOpen]  = useState(false);

	const handleExpandClick = () => {
	    setExpanded(!expanded);
	};

	console.log(titulo)

	return (
		<React.Fragment>

		<Modal
	        isShowing={isShowing}
	        hide={toggle}
	        layerSlider={layerSlider}
	        titulo={titulo}
	      />

        <Card className={classes.card}>
	    	<CardActionArea
	    		onClick = {toggle}
	    	>
	    	<GlobalCss />
	          <CardHeader
	            avatar={
	              <Avatar aria-label={"recipe"} className={classes.avatar}>
	                {indice}
	              </Avatar>
	            }
	            action={
	            	<div className={classes.edb}></div>
	            }
	            subheader={subheader}
	          />

				<CardMedia
	            className={classes.media}
	            image={img}
	            title={titulo}
	          />
	        
	          <CardContent>
	            <Typography variant="body2" color="textSecondary" component="p">
	             	{introduccion}
	            </Typography>
	          </CardContent>

        	</CardActionArea>

	          <CardActions>
				<IconButton aria-label="add to favorites">
	              <FavoriteIcon />
	            </IconButton>
	            <IconButton aria-label="share">
	              <ShareIcon />
	            </IconButton>	          
	            <IconButton
		              className={clsx(classes.expand, {
		                [classes.expandOpen]: expanded,
		              })}
		              onClick={handleExpandClick}
		              aria-expanded={expanded}
		              aria-label="show more"
		            >
		              <ExpandMoreIcon />
	            </IconButton>
	          </CardActions>


	          <Collapse in={expanded} timeout="auto" unmountOnExit>
	            <CardContent>
	              <Typography paragraph>{titulo}</Typography>
	              <Typography paragraph>{descripcion}</Typography>	              
	            </CardContent>
	          </Collapse>
        </Card>
		</React.Fragment>
	  );
}
