const useStyles = makeStyles(styles);
export default function Galeria(props) {

  let idioma = checkIdioma();
  let servicios = config.servicios
  let serviciosIdioma = config.servicios[idioma]
  let contenidoServicios = config.servicios[idioma].contenido;
  
  const classes = useStyles();
  // let {xs=12,sm=6,md=4} = servicios.columnas
  const [xs, setXs] = useState(servicios.columnas.xs || 12);
  const [sm, setSm] = useState(servicios.columnas.sm || 6);
  const [md, setMd] = useState(servicios.columnas.md || 4);
  return (
    <React.Fragment>
      <div className={classes.section} id={"seccion-productos"}>
        <GridContainer justify="center">
          <GridItem xs={12} sm={12} md={8}>
            <h2 className={classes.title}>{serviciosIdioma.titulo}</h2>
            <h5 className={classes.description}>
              {serviciosIdioma.leyenda}
            </h5>
          </GridItem>
        </GridContainer>
      <div className={classes.section}>
            <GridContainer >
            {
              contenidoServicios.map( (contenido,index) => {
                return(
                  <GridItem xs={xs} sm={sm} md={md} key={"key_GridItem"+index}>
                      <CustomCard contenido={contenido} key={"key_CustomCard_"+index}/>                 
                  </GridItem>
                )
              })
            }                           
            </GridContainer>
        </div>
      </div>
    </React.Fragment>
  );
}
