const dashboardRoutes = [];

const useStyles = makeStyles(styles);

export default function Principal(props){
	const [idioma,setIdioma] = useState(localStorage.getItem("idioma") || "es");
	const [contenido, setContenido] = useState(config);

	function handleClick(e){
		let idioma = (e.target.type==="button")?idioma= e.target.id:e.target.parentNode.parentNode.id;
		if(idioma){
		setIdioma(idioma);
		localStorage.setItem("idioma",idioma)			
		//console.log(idioma)
		}else{
			//console.log("Ocurrio algo "  +  idioma)
		}
	}

	const classes = useStyles();
	const { ...rest } = props;
	console.log(idioma)
	let contenidoPresentacion = contenido.header[idioma].header;
	let contenidoContactos = contenido.contactos[idioma];

	return(
		<Provider value={{
			idioma : idioma,
			handleClick : handleClick,
			contenido : contenido
		}}>
			<div id="seccion-principal">
				<Element name={"1"}>
					<PresentacionHeaderAndLayerSlider classes={classes} {...rest} contenido={contenidoPresentacion}/>
				</Element>
			    
			      <div className={classNames(classes.main, classes.mainRaised)}>
			      		<Element name={"seccion-nosotros"}>
							<Nosotros/>
			      		</Element>
			      		<Element name={"seccion-servicios"}>
							<Servicios />
			      		</Element>
			      		<Element name={"seccion-obras"}>
							{/*<Objetivos/>*/}
							<Galeria/>
							{/*<Relatos contenido={contenidoRelatos}/>*/}
							{/*<Muestras/>*/}
						</Element>				
			      		<Element name={"seccion-contactos"}>
								<Contactos contenido={contenidoContactos}/>
			      		</Element>
			      </div>
	      		<Footer2 />

			</div>
		</Provider>
	)
}