import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import Card from "components/Card/Card.js";

import styles from "assets/jss/material-kit-react/views/landingPageSections/nosotrosStyle.js";

import styled, { keyframes } from "styled-components";
import BounceInLeft from "@bit/formidablelabs.react-animations.bounce-in-left";
import FadeInRightBig from "@bit/formidablelabs.react-animations.fade-in-right-big";

const BounceInLeftAnimation = keyframes`${BounceInLeft}`;
const BounceInLeftDiv = styled.div`
  animation: 5s ${BounceInLeftAnimation};
`;

const FadeInRightBigAnimation = keyframes`${FadeInRightBig}`;
const FadeInRightBigDiv = styled.div`
  animation: 7s ${FadeInRightBigAnimation};
`;


const useStyles = makeStyles(styles);

export default function Relatos(props){
	const classes = useStyles();
	const {contenido} = props;
	//cambiar urgente este pedazo de sorete
	const content = (d,i) => {
			if(i%2===0){
				return(
					<React.Fragment>
				        <GridItem xs={12} sm={12} md={6}>
							<BounceInLeftDiv>
					        	<img src={d.img} alt="" className={classes.muestras}/>
	  						</BounceInLeftDiv>
				        </GridItem>
				        <GridItem xs={12} sm={12} md={6}>
						        <FadeInRightBigDiv className={classes.description}>
										<h5>
											{d.descripcion}
										</h5>
						        </FadeInRightBigDiv>
				        </GridItem>
					</React.Fragment>
				)
			}else{
				return(
					<React.Fragment>
				        <GridItem xs={12} sm={12} md={6} className={classes.description}>
							<h5>{d.descripcion}</h5>
				        </GridItem>					
				        <GridItem xs={12} sm={12} md={6}>
							<FadeInRightBigDiv>
							    	<img src={d.img} alt="" className={classes.muestras}/>
							</FadeInRightBigDiv>
				        </GridItem>
					</React.Fragment>
				)				
			}
	}
  	const items = () => {
  		let it = contenido.map((d,i) => {
  				console.log(classes.relatos)
  				return(
			      <GridContainer className={classes.relatos} key={"relato_"+i}>
			      	{content(d,i)}
			      </GridContainer>
  				)
  		})
  		return it;
  	}
	return(
			    <div className={classes.section}>
			    	{items()}
			    </div>
	)
}
