const DataLsP =  "durationin:1000;delayin:3300;rotatein:20;rotatexin:30;scalexin:1.5;scaleyin:1.5;transformoriginin:left 50% 0;durationout:750;rotateout:20;rotatexout:-30;scalexout:0;scaleyout:0;transformoriginout:left 50% 0;"
function OverrideStyle(config){
	return Object.assign({},styleP,config)
}
function OverrideDataLsp(config){
	handleStringToObject(config)
	//return DataLsP
	return handleStringToObject(config);
}

function handleObjectToString(obj){
	let string =""
	Object.keys(obj).forEach((key) => {
		string += key+":"+obj[key]+";"   
	})
	return string;
}

function handleStringToObject(config){
	if(!config)
		return 
	let array = DataLsP.split(";");
	let object = {}

	array.map( (value,index) => {
		let key = value.split(":")[0]
		let valor = value.split(":")[1]
		if(key && valor){
	 		object[key] = valor 
		}
	});
	let obj = {}
	obj = Object.assign(object,config)
	return handleObjectToString(obj)
}

const Slide = (props) => {
	let {clases,slider,children} = props
	return(
		<React.Fragment>
			<div className={"ls-slide"} data-ls={"slidedelay: 7000; transition2d: 75,79;"}>
				{Child(children)}
			</div>
		</React.Fragment>		
	)	
}

function Child(children){
	let input= ""
	let child = children.map((registro,index) => {
		if(registro["img"])
			return <img  key={"child_index_"+index + "_"+Math.random().toString()+"_img"} src={registro.img} className={"ls-bg"} alt={"Slide background"}/>
		if(registro["p"])
			return  <p key={"child_index_"+index + "_"+Math.random().toString()+"_p"} className={"ls-l"} style={OverrideStyle(registro.config || {})}
									    data-ls={OverrideDataLsp(registro.config || {})}>
									   {registro["p"]} 
									</p>
	})
	return(
			<React.Fragment>
				{child}
			</React.Fragment>				
	)	
}

function PrintLayer(layerSlider){
	return(
		<React.Fragment>
			{layerSlider.sliders.map((currentValue,index,array) => <Slide key={"layerSlider_key_"+Math.random().toString()} slider={currentValue} children={currentValue.children}/>)}
		</React.Fragment>			
	)
}

