import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// nodejs library that concatenates classes
import classNames from "classnames";
import { checkIdioma } from '../../../../services/services';
import contenido from '../../../../config/config';
// @material-ui/icons

import styles from "assets/jss/material-kit-react/views/landingPageSections/contactosStyle.js";
import Datos from './Datos';

const useStyles = makeStyles(styles);

export default function Contactos(props) {
  const classes = useStyles();
  let idioma = checkIdioma();
  let contenidoContactos = contenido.contactos[idioma];

  return (
        <Datos classes={classes} contenido={contenidoContactos}/>
  );
}
