import React from "react";
import HomeIcon from '@material-ui/icons/Home';
import Button from "components/CustomButtons/Button.js";
import ListItem from "@material-ui/core/ListItem";
import Tooltip from "@material-ui/core/Tooltip";
import ContactMailIcon from '@material-ui/icons/ContactMail';
import StarIcon from '@material-ui/icons/Star';
import People from '@material-ui/icons/People';
import Build from '@material-ui/icons/Build';
import ReactCountryFlag from "react-country-flag";
import { Link as LinkScroll } from 'react-scroll';
import Warning from '@material-ui/icons/Warning';
import NewReleases from '@material-ui/icons/NewReleases';


//importamos el contexto
import { Consumer } from '../../Context';

import { checkIdioma } from '../../services/services';
import config from '../../config/config';

export default function Link (props) {
	const {classes} = props;

	let idioma = checkIdioma();
	let contenidoHeaderLinks = config.header[idioma].headerLinks;


	const renderIcon = (registro) => {
		switch(registro.icon){
			case "HomeIcon":
				return(
						<React.Fragment>
				          <HomeIcon className={classes.icons} /> {registro.titulo}
						</React.Fragment>
			)
			break;
			case "People":
			return(
						<React.Fragment>
				          <People className={classes.icons} /> {registro.titulo}
						</React.Fragment>
			)
			break;
			case "Build":
			return(
						<React.Fragment>
				          <Build className={classes.icons} /> {registro.titulo}
						</React.Fragment>
			)
			break;
			case "Warning":
			return(
						<React.Fragment>
				          <Warning className={classes.icons} /> {registro.titulo}
						</React.Fragment>
			)
			case "NewReleases":
			return(
						<React.Fragment>
				          <NewReleases className={classes.icons} /> {registro.titulo}
						</React.Fragment>
			)			
			break;					
			case "ContactMailIcon":
			return(
						<React.Fragment>
				          <ContactMailIcon className={classes.icons} /> {registro.titulo}
						</React.Fragment>
			)
			break;								
			default :
				return(
							<React.Fragment>
					          <HomeIcon className={classes.icons} /> {registro.titulo}
							</React.Fragment>
				)
			break;
		}

	}

	const renderButton = (registro) => {
		if(registro.button && registro.button.traducir){
			let code = registro.button.traducir;
			// console.log(code)
			// href={registro.button.href}
			return(
						<Consumer>
							{ ( {idioma, handleClick} ) => {
								return(
							          <Button
							            id={registro.button.id} 
							            name={registro.button.id}
							            className={classes.navLink}
							          	title={registro.tooltip.titulo}
							            color="transparent"
							            data-id={registro.button["data-id"]}
							            onClick={handleClick}
							          >
							            <ReactCountryFlag 
										    styleProps={{
										        width: '20px',
										        height: '20px'
										    }}
							            	code={code}
							            	svg/>
							          </Button>
								)
							}}
						</Consumer>
			)
		}else{
			return(
			          <Button
			            id={registro.button.id} 
			            name={registro.button.id}
			            color="transparent"
			            className={classes.navLink}
			          >
			            <i className={classes.socialIcons +" "+ registro.icon} />
			          </Button>			
				)
		}
	}

	const fnScroller = (name) => {
		console.log(name)
	}
	const bodyLink = (registro,type="default") => {
		switch(type){
			case "default":
			return(
				      <ListItem className={classes.listItem} key={"index_" + registro.titulo} >
				        <LinkScroll
				          to={registro.id}
				          smooth={true}
				          spy={true}
				          offset={-20}
				          color="transparent"
				          className={classes.navLink}
				          onClick={
				          		(e) => {fnScroller(registro.id)}
				          }
				        >
				          {renderIcon(registro)}
				        </LinkScroll>	
			      	</ListItem>
			)
			break;
			case "social":
				return(
				      <ListItem className={classes.listItem} key={"index_" + registro.titulo} >
						<Tooltip
				          id={registro.tooltip.id}
				          title={registro.tooltip.titulo}
				          placement={window.innerWidth > 959 ? "top" : "left"}
				          classes={{ tooltip: classes.tooltip }}
				        >
						{renderButton(registro)}
				        </Tooltip>	
			      	</ListItem>
				)
			break;
			default:
			break;
		}
	}
	const renderLink = () => {
		let link = contenidoHeaderLinks.map( (registro,index) => {
			let Tag = registro.icon;
		      return bodyLink(registro,registro.type);
			})
		return link;
	}

	return(
				<div>{renderLink()}</div>
			)
} 