import React from 'react';
import ReactDOM from 'react-dom';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';


import LayerSliderStyle from "assets/jss/material-kit-react/components/LayerSliderStyle.js";
import LayerSlider from '../LayerSliders/LayerSlider2';
import { makeStyles } from "@material-ui/core/styles";
const useStyles = makeStyles(LayerSliderStyle);


const Modal = ({ isShowing, hide, layerSlider, titulo }) => { 
  const layerClass = useStyles();
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('lg'));

  return (
      isShowing ? ReactDOM.createPortal(
        <React.Fragment>
          <div>
            <Dialog
              open={isShowing}
              onClose={hide}
              fullWidth={false}
              maxWidth={"xl"}
              scroll={"body"}
              aria-labelledby="responsive-dialog-title"
            >
              <DialogTitle id="responsive-dialog-title">{titulo}</DialogTitle>
              <DialogContent>
{/*                <DialogContentText>
                  Aca va a ir el layerSlider
                </DialogContentText>
*/}
              <LayerSlider clases={layerClass} layerSlider={layerSlider}/>
              </DialogContent>
              <DialogActions>
                <Button onClick={hide} color="primary" autoFocus>
                  Cerrar
                </Button>
              </DialogActions>
            </Dialog>
          </div>

        </React.Fragment>, document.body
      ) : null
  )} 

export default Modal;