import React , {useState, useEffect} from "react";

//importamos el contexto
import { Consumer } from '../../../../Context';

import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import foto from "assets/img/muestras/almacenaje.jpg";
import container from "assets/img/muestras/container.jpg";
import maquina from "assets/img/muestras/maquina.jpg";

import styled, { keyframes } from "styled-components";
import BounceInLeft from "@bit/formidablelabs.react-animations.bounce-in-left";
import FadeInRightBig from "@bit/formidablelabs.react-animations.fade-in-right-big";

import { checkIdioma } from '../../../../services/services';

const BounceInLeftAnimation = keyframes`${BounceInLeft}`;
const BounceInLeftDiv = styled.div`
  animation: 2s ${BounceInLeftAnimation};
`;

const FadeInRightBigAnimation = keyframes`${FadeInRightBig}`;
const FadeInRightBigDiv = styled.div`
  animation: 1s ${FadeInRightBigAnimation};
`;

export default function Descripcion (props) {
	const {classes} =  props;

	return(
			<Consumer>
				{ ({idioma, contenido}) => {
					let titulo = contenido.nosotros[idioma].titulo;
					let descripcion = contenido.nosotros[idioma].descripcion;

					return(
						<React.Fragment>
						    <div className={classes.section} id={"seccion-nosotros"}>
						      <GridContainer justify="center">
						        <GridItem xs={12} sm={12} md={8}>
									<BounceInLeftDiv>
							          <h2 className={classes.title}>{titulo}</h2>
			  						</BounceInLeftDiv>
						        <FadeInRightBigDiv>
							          <h5 className={classes.description}>
							           {descripcion}
							          </h5>
							        </FadeInRightBigDiv>
						        </GridItem>
						      </GridContainer>
						    </div>
						</React.Fragment>
					)
				}}
			</Consumer>
	)
}